body {
  text-align: center;
}

h1 {
  font-family: Monoton;
  font-size: 40px;
  margin: 0.2em;
}

h2 {
  margin: 1em 0 0.25em;
}

h2:first-of-type {
  margin-top: 0.5em;
}

table,
tr,
td {
  border: 1px solid black;
}

table {
  border-collapse: collapse;
  margin: 0 auto;
}

tr {
  height: 1.35em;
}

td {
  width: 1.35em;
}

input[type='number'] {
  width: 6em;
}

.endgame-sub {
  font-weight: bold;
}

.endgame-message {
  font-weight: bold;
  font-size: 2em;
}

.losing-message {
  color: red;
}

.winning-message {
  color: green;
}

#instructions {
  font-style: italic;
}
